<template>
  <div
    class="oval-loading"
    :style="{ width: `${size}px`, height: `${size}px` }"
  >
    <svg
      :width="38"
      :height="38"
      viewBox="0 0 38 38"
      xmlns="http://www.w3.org/2000/svg"
      :style="{ zoom }"
    >
      <g fill="none" fill-rule="evenodd">
        <g transform="translate(1 1)" stroke-width="2">
          <circle
            stroke-opacity=".5"
            cx="18"
            cy="18"
            r="18"
            :stroke="status === 'full' ? color : layerColor"
          />
          <path
            v-show="status === 'loading'"
            d="M36 18c0-9.94-8.06-18-18-18"
            :stroke="color"
            class="oval-path"
          >
            <animateTransform
              attributeName="transform"
              type="rotate"
              from="0 18 18"
              to="360 18 18"
              dur="1s"
              repeatCount="indefinite"
            />
          </path>
        </g>
      </g>
    </svg>
    <div class="oval-content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
const STATUS = ['loading', 'stop', 'finish'];

export default {
  props: {
    size: {
      type: [Number, String],
      default: 36,
    },
    color: {
      type: String,
      default: '#2C5CF7',
    },
    layerColor: {
      type: String,
      default: '#EBF1FF',
    },
    status: {
      validator: function(value) {
        // 传入数字
        if (/^\d+$/gi.test(value)) {
          return !!STATUS[value];
        } else {
          // 只允许传入三个状态：loading：转动，stop：停止，会隐藏转动的圈，finish：停止转动，铺满
          return ~STATUS.indexOf(value);
        }
      },
    },
  },

  data() {
    return {
      zoom: 1,
    };
  },

  created() {
    this.zoom = this.size / 36;
  },
};
</script>

<style lang="scss" scoped>
.oval-path {
  stroke-linecap: round;
}

.oval-loading {
  position: relative;
  text-align: center;

  & > svg {
    position: absolute;
    top: 0;
    left: 0;
  }

  .oval-content {
    position: absolute;
    top: 50%;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    transform: translateY(-50%);
  }
}
</style>
