<template>
  <div class="tenant-list-warpper">
    <app-header title="切换建设单位"> </app-header>
    <div class="blank"></div>

    <div class="tenant-list-content">
      <tenant-item
        v-for="item in tenantList"
        :key="item.tenantCode"
        :name="`${item.tenantName}(${item.tenantCode})`"
        @click="changeTenant(item)"
      >
        <i
          class="super-app-iconfont super-app-icon-selected light"
          v-if="item.tenantCode === currentTenantCode"
        ></i>
      </tenant-item>
      <div class="disable-title" v-if="verifyList.length">申请中的建设单位</div>
      <tenant-item
        v-for="item in verifyList"
        :key="item.code"
        :name="item.tenantName"
        @click="openVerifyPage(item)"
      >
        <div
          :class="{
            'wait-pending-text': item.approveStatus === allStatus.waiting,
            'reject-text': item.approveStatus === allStatus.reject,
          }"
        >
          {{ statusText[item.approveStatus] }}
        </div>
      </tenant-item>
    </div>
    <div class="footer">
      <app-button secondary @click="openBindPage">绑定其他用户</app-button>
      <app-button @click="openAuthPage">申请建设单位认证</app-button>
    </div>
    <van-popup
      v-model="show"
      :close-on-click-overlay="false"
      round
      position="bottom"
      :style="{ height: '45%' }"
    >
      <div class="circle-con">
        <!-- <van-circle
          v-model="currentRate"
          :rate="30"
          :stroke-width="60"
          :clockwise="clockwise"
          size="160px"
          :color="{ '0%': '#2c5cf7', '100%': '#2C5CF7' }"
          layer-color="#EBF1FF"
        > -->
        <oval-loading size="160" :status="changeStatus">
          <div
            class="circle-text"
            :class="{ light: changeStatus === 'finish' }"
          >
            <template v-if="changeStatus === 'loading'">
              <i class="super-app-iconfont super-app-icon-change-light" />
              切换中...
            </template>
            <template v-else-if="changeStatus === 'finish'">
              <i class="super-app-iconfont super-app-icon-selected" />
              切换成功
            </template>
            <template v-else-if="changeStatus === 'stop'">
              <i class="super-app-iconfont super-app-icon-fail" />
              切换失败
            </template>
          </div>
        </oval-loading>
        <div class="retry-btn" v-if="changeStatus === 'stop'">
          <app-button class="cancel-btn" @click="show = false">取消</app-button>
          <app-button @click="doChange">重试</app-button>
        </div>
        <!-- </van-circle> -->
      </div>
    </van-popup>
  </div>
</template>
<script>
import tenantItem from '@src/widget/tenant-list/tenant-item';
// import AppFooterButton from '@components/app-footer-button';
import AppButton from '@components/app-button';
// import { Circle } from 'vant';
import OvalLoading from '@components/oval-loading';
import TenantService from '@services/tenant-service';
import SsoCenterService from '@services/sso-center-service';
import UserService from '@services/user-service';
import STATIC from '@config/static';

export default {
  components: {
    tenantItem,
    // AppFooterButton,
    AppButton,
    // [Circle.name]: Circle,
    OvalLoading,
  },

  data() {
    return {
      show: false,
      currentRate: 0,
      tenantList: [],
      verifyList: [],
      currentTenantCode: window.TENANT_CONFIG.TENANT_CODE,
      currentItem: null,
      clockwise: true,
      timer: null,
      allStatus: STATIC.SSO_APPROVE_STATUS,
      statusText: STATIC.SSO_APPROVE_STATUS_TEXT,
      changeStatus: 'loading', // loading：切换中，stop：失败，finish：成功
    };
  },

  activated() {
    this.init();
  },

  methods: {
    init() {
      this.currentItem = null;
      this.changeStatus = 'loading';
      this.show = false;
      this.$toast.loading({
        duration: 0, // 持续展示 toast
        overlay: true,
        forbidClick: true, // 禁用背景点击
        loadingType: 'spinner',
        message: '加载中...',
      });
      return TenantService.getTenantList()
        .then(list => {
          this.$toast.clear();
          console.log('list =====> ', list);
          this.tenantList = list.tenantList || [];
          this.verifyList = list.verifyTenantList || [];
          this.currentTenantCode = window.TENANT_CONFIG.TENANT_CODE;
          console.log(this.currentTenantCode);
        })
        .catch(() => {
          this.$toast.clear();
          this.tenantList = [];
          this.verifyList = [];
          this.currentTenantCode = window.TENANT_CONFIG.TENANT_CODE;
        });
    },

    doChange() {
      this.show = true;
      return TenantService.changeTenant(
        this.currentItem.tenantCode,
        this.currentItem.tenantName
      )
        .then(() => {
          this.changeStatus = 'finish';
          this.currentItem = null;
          console.log('TenantService.changeTenant ===== ');
          // 返回“我”
          setTimeout(() => {
            this.show = false;
            this.$router.push({ path: '/user' });
          }, 500);
        })
        .catch(() => {
          this.changeStatus = 'stop';
        });
    },

    changeTenant(item) {
      // 租户不变，直接返回
      if (this.currentTenantCode === item.tenantCode) {
        return false;
      }
      this.currentItem = item;
      this.changeStatus = 'loading';
      this.$dialog
        .confirm({
          message: `确认切换开发商到【${item.tenantName}(${item.tenantCode})】吗？切换后只能处理该开发商业务`,
          confirmButtonText: '确认切换',
        })
        .then(() => {
          this.doChange();
          console.log(item, 'item');
        });
    },

    openAuthPage() {
      SsoCenterService.openPage('/auth/index', {
        tenantCode: '',
        tenantName: '',
      });
    },

    openBindPage() {
      SsoCenterService.openPage('/bind-user');
    },

    openVerifyPage(item) {
      // 获取租户token
      return UserService.token2token(item.tenantCode, false).then(token => {
        SsoCenterService.openPage('/auth/auditing', {
          token: token,
          tenantCode: item.tenantCode,
          tenantName: item.tenantName,
          showLeftBtn: 1,
        });
      });
    },
  },
};
</script>
